*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  box-sizing: border-box;
}
body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-width: 320px;
  max-width: 1920px;
  min-height: 100vh;
  font-family: "Montserrat", "arial", sans-serif;
  font-style: normal;
  background-color: #e5e5e5;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden,
input[type="file"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  border: 0;
  overflow: hidden;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.button {
  position: relative;
  display: inline-block;
  border: none;
  cursor: pointer;
  font-family: inherit;
}
.page {
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  flex-direction: column;
  min-height: 100vh;
  background-image: url("../img/antarctic_desktop.png");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  background-image: url("../img/antarctic_desktop@2x.png");
}
@media (max-width: 1023px) {
  .page {
    background-image: url("../img/antarctic_tablet.png");
  }
}
@media (max-width: 767px) {
  .page {
    background-image: url("../img/antarctic_mobile.png");
    background-size: cover;
    background-position: center;
  }
}
.page-header {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 35px 103px 0;
}
.page-header__logo {
  margin-right: auto;
  color: #F9FBFD;
}
.page-header__toggle {
  display: none;
  margin: 0;
  padding: 0;
  border: 0;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 30px 45px 0;
  }
}
@media (max-width: 767px) {
  .page-header {
    position: relative;
    padding: 25px 15px 0;
  }
  .page-header--nojs {
    padding: 0;
  }
  .page-header .page-header__logo {
    color: #F9FBFD;
  }
  .page-header--nojs .page-header__logo {
    position: absolute;
    top: 20px;
    left: 15px;
    color: #011C40;
  }
  .page-header--menu-open .page-header__logo {
    color: #011C40;
  }
  .page-header--nojs .page-header__nav-list {
    display: block;
    position: static;
    width: 100%;
  }
  .page-header--menu-open .page-header__nav-list {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
  .page-header .page-header__toggle {
    display: block;
    width: 24px;
    height: 12px;
    z-index: 10;
    background-image: url("../img/menu.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 24px 12px;
    background-color: inherit;
  }
  .page-header .page-header__toggle:hover {
    cursor: pointer;
    opacity: 0.5;
  }
  .page-header--nojs .page-header__toggle {
    display: none;
  }
  .page-header--menu-open .page-header__toggle {
    display: block;
    width: 13px;
    height: 13px;
    background-image: url("../img/close.svg");
    background-size: 13px 13px;
  }
}
.logo {
  z-index: 10;
}
.logo__image {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .logo__image {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__image {
    width: 42px;
    height: 22px;
  }
}
.main-nav {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 80%;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item:not(:last-child) {
  margin-right: 33px;
}
.main-nav__link {
  color: #F9FBFD;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
}
.main-nav__link:hover,
.main-nav__link:focus {
  cursor: pointer;
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .main-nav__item:not(:last-child) {
    margin-right: 23px;
  }
  .main-nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    display: none;
    flex-direction: column;
    padding: 93px 15px 65px;
    color: #011C40;
    background-color: #F9FBFD;
  }
  .main-nav--footer {
    display: flex;
    margin: 0;
    padding: 0;
    margin-bottom: 30px;
    background-color: inherit;
  }
  .main-nav__item:not(:last-child) {
    margin-left: 0;
    margin-bottom: 30px;
  }
  .main-nav__item--footer:not(:last-child) {
    margin-bottom: 23px;
  }
  .main-nav__link {
    font-size: 18px;
    line-height: 22px;
    color: #011C40;
  }
  .main-nav__link--footer {
    font-size: 12px;
    line-height: 15px;
    color: #F9FBFD;
  }
}
.page-main {
  flex-grow: 1;
}
.page-footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-shrink: 0;
  margin: 0;
  padding: 0;
  padding: 50px 103px;
  background-color: #011C40;
}
.page-footer__logo {
  margin-right: 40px;
  color: #F9FBFD;
}
.page-footer__nav-list {
  margin-right: 40px;
}
.page-footer__social {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
@media (max-width: 1023px) {
  .page-footer {
    padding: 50px 45px;
  }
  .page-footer__logo {
    margin-right: 25px;
  }
}
@media (max-width: 767px) {
  .page-footer {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 40px 15px;
  }
  .page-footer__logo {
    margin: 0;
    padding: 0;
    margin-bottom: 30px;
    color: #F9FBFD;
  }
}
.social {
  margin: 0;
  padding: 0;
  margin-left: auto;
  list-style: none;
  min-width: 20%;
}
.social__item:not(:last-child) {
  margin-right: 33px;
}
.social__link:hover,
.social__link:focus {
  cursor: pointer;
  opacity: 0.5;
}
.social__link-image {
  color: #F9FBFD;
}
@media (max-width: 1023px) {
  .social__item:not(:last-child) {
    margin-right: 23px;
  }
  .social__link-image--facebook {
    width: 9px;
    height: 16px;
  }
  .social__link-image--instagram {
    width: 16px;
    height: 16px;
  }
  .social__link-image--twitter {
    width: 16px;
    height: 13px;
  }
}
@media (max-width: 767px) {
  .social {
    margin: 0;
    padding: 0;
  }
  .social__link-image--facebook {
    width: 9px;
    height: 16px;
  }
  .social__link-image--instagram {
    width: 16px;
    height: 16px;
  }
  .social__link-image--twitter {
    width: 16px;
    height: 13px;
  }
}
